.signup-div {
  display: flex;
  justify-content: center;
  width: 75%;
}
.rightsignup-section {
  width: 40%;
  background-color: #ffffff;
}
.leftsignup-section {
  width: 50%;
  background-color: #3dbb94;
}
.leftside-signinuser {
  width: 40%;
  background-color: #3dbb94;
}
.rightside-signinuser {
  width: 50%;
  background-color: white;
}
.userside-signupheader {
  font-style: italic;
  color: #3dbb94;
  padding: 20px;
}
.leftside-userheader {
  font-style: italic;
  padding-top: 20px;
  color: white;
}
.input-username,
.input-email,
.input-password {
  border: none;
  width: 250px;
  background-color: #f5f5f5;
  border-radius: 3rem;
  padding: 8px;
  text-align: center;
}

.signup-button {
  text-decoration: none;
  border: 1px solid #ffffff;
  border-radius: 4rem;
  background-color: #3dbb94;
  padding: 8px 30px;
  color: #ffffff;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.signup-button:hover {
  background-color: #ffffff;
  color: #3dbb94;
  border: 1px solid #3dbb94;
}
.signup-button:hover .span {
  color: #3dbb94;
  border-radius: 1px solid #3dbb94;
}
.span {
  color: white;
  text-decoration: none;
}

.fas {
  color: #333333;
}

/*  */
.input-icons i {
  position: absolute;
}

.input-icons {
  padding: 10px;
}

.icon {
  padding: 10px 10px;
  color: gray;
  text-align: center;
}
